<template>
  <div id="Container">
    <div id="showBeforeSearch" v-show="!isSearching">
      <img src="http://110.40.191.101/myIcon/min.png" alt="" />
      <br />
      <br />
      <p>By GeorgeZ@YuZhang Normal university</p>
      <p>Supported by GitHub</p>
    </div>
    <div id="showBeforeSearch" v-show="isError">
      <h1>出了一点小问题~</h1>
      <p>{{ errorMsg }}</p>
    </div>
    <div id="Loading" v-show="isLoading">
      <img src="http://110.40.191.101/turntableImg/loading_2.gif" alt="" />
    </div>
    <userCard />
  </div>
</template>
<script>
import userCard from "./userCard.vue";
export default {
  name: "cardContainer",
  components: {
    userCard,
  },
  data() {
    return {
      isSearching: false,
      isError: false,
      errorMsg: "",
      isLoading: false,
    };
  },
  mounted() {
    this.$bus.$on("resetResult", () => {
      this.isSearching = true;
    });
    this.$bus.$on("changeIsErrorStatus", (errorMsg) => {
      this.isSearching = true;
      this.isError = true;
      this.errorMsg = errorMsg;
    });
    this.$bus.$on("changeIsErrorForFalse", () => {
      this.isError = false;
    });
    this.$bus.$on("changeIsLoadingForTrue", () => {
      this.isLoading= true;
    });
     this.$bus.$on("changeIsLoadingForFalse", () => {
      this.isLoading= false;
    });
  },
};
</script>
<style scoped>
#Container {
  width: 800px;
  min-height: 320px;
  margin: 0 auto;
  margin-top: 30px;
  padding: 10px;
  box-sizing: border-box;
  border: 1px #ddd solid;
  overflow: hidden;
}
#showBeforeSearch {
  width: 100%;
  padding: 10px;
  text-align: center;
}
#showBeforeSearch p,
h1 {
  color: #bbb;
}
#Loading {
  text-align: center;
}
</style>