<template>
  <div id="mySearch">
    <div class="jumbotron modifyPosition">
      <h1>Search For Github Users</h1>
      <p>
        Find a GitHub user and click the user card to view the details of the
        user
      </p>

      <input
        type="text"
        placeholder="Search for..."
        v-model="keyword"
        @keyup.enter="go"
      />
      <span>
        <button type="button" @click="go">Go!</button>
      </span>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "mySearch",
  data() {
    return {
      keyword: "",
    };
  },
  methods: {
    go: function () {
      if (this.keyword.trim() == "") return alert("无效搜索（关键词为空）");
      this.$bus.$emit("resetResult");
      this.$bus.$emit("changeIsSearchingStatus");
      this.$bus.$emit("changeIsLoadingForTrue");
      axios.get(`https://api.github.com/search/users?q=${this.keyword}`).then(
        (response) => {
          if (response.data.items.length <= 0) {
            this.$bus.$emit("changeIsLoadingForFalse");
            this.$bus.$emit(
              "changeIsErrorStatus",
              "没有找到与输入关键词相关的用户"
            );
          } else {
            this.$bus.$emit("changeIsLoadingForFalse");
            this.$bus.$emit("changeIsErrorForFalse");
            this.$bus.$emit("getPartResult", response.data.items);
          }
        },
        (error) => {
          this.$bus.$emit("changeIsLoadingForFalse");
          this.$bus.$emit("changeIsSearchingStatus");
          this.$bus.$emit("changeIsErrorStatus", error.message);
        }
      );
      this.keyword = "";
    },
  },
};
</script>
<style scoped>
.modifyPosition {
  margin: 0 auto;
  padding-left: 75px;
  text-align: center;
}
.modifyPosition_2 {
  margin: 0 auto;
}
</style>