<template>
  <div>
    <div id="userCard" v-for="user in partResult" :key="user.login">
      <a :href="user.html_url">
        <img :src="user.avatar_url" alt="" />
      </a>
      <p>{{ user.login }}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "userCard",
  data() {
    return {
      partResult: [],
    };
  },
  mounted() {
    this.$bus.$on("getPartResult", (partResult) => {
      this.partResult = partResult;
    });
    this.$bus.$on("resetResult", () => {
      this.partResult = [];
    });
  },
};
</script>
<style scoped>
#userCard {
  width: 240px;
  height: 235px;
  border: 1px solid #eee;
  float: left;
  margin-left: 15px;
  margin-top: 10px;
  box-sizing: border-box;
  padding: 20px;
  text-align: center;
}
#userCard a img {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  cursor: pointer;
}
#userCard p {
  margin-top: 10px;
  color: #888;
  font-weight: 600;
  cursor: pointer;
}
</style>